import { Bar } from "react-chartjs-2";
import React from "react";
import {
  Chart as ChartJS,
  CategoryScale,
  LinearScale,
  BarElement,
  Title,
  Tooltip,
  Legend,
} from "chart.js";

ChartJS.register(
  CategoryScale,
  LinearScale,
  BarElement,
  Title,
  Tooltip,
  Legend
);

function HistoryBar({ datas }) {
  const options = {
    plugins: {
      title: {
        display: true,
        text: " ",
      },
    },
    responsive: true,
    scales: {
      x: {
        stacked: true,
      },
      y: {
        stacked: true,
      },
    },
  };
  const labels = ["", "", "", "", ""];
  const cardSum = [0, 0, 0, 0, 0];
  const cashSum = [0, 0, 0, 0, 0];
  datas.forEach((one) => {
    let idx = labels.indexOf(one.category);

    if (idx !== -1) {
      cashSum[idx] += one.cashAmt ?? 0;
      cardSum[idx] += one.cardAmt ?? 0;
    }
  });

  const data = {
    labels,
    datasets: [
      {
        label: "",
        data: cashSum,
        backgroundColor: "rgb(255,99,132)",
      },
      {
        label: "",
        data: cardSum,
        backgroundColor: "rgb(2,255,150)",
      },
    ],
  };

  return <Bar data={data} options={options} />;
}

export default HistoryBar;
